.primary-button:active:not(:disabled) {
  transform: translateY(0);
}

.primary-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

/* DataPingo Button Styles */
.datapingo-button {
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 10px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  text-transform: none;
  font-size: 0.95rem;
}

.datapingo-button.primary {
  background: linear-gradient(135deg, var(--datapingo-primary) 0%, var(--datapingo-accent) 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(37, 99, 235, 0.2);
}

.datapingo-button.secondary {
  background: linear-gradient(135deg, var(--datapingo-secondary) 0%, var(--datapingo-secondary-light) 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(16, 185, 129, 0.2);
}

.datapingo-button.accent {
  background: linear-gradient(135deg, var(--datapingo-accent) 0%, var(--datapingo-accent-light) 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(139, 92, 246, 0.2);
}

.datapingo-button.warning {
  background: linear-gradient(135deg, var(--datapingo-orange) 0%, var(--datapingo-orange-light) 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(245, 158, 11, 0.2);
}

.datapingo-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
  transition: left 0.5s;
}

.datapingo-button:hover:not(:disabled) {
  transform: translateY(-2px);
  filter: brightness(1.1);
}

.datapingo-button.primary:hover:not(:disabled) {
  box-shadow: 0 8px 25px rgba(37, 99, 235, 0.3);
}

.datapingo-button.secondary:hover:not(:disabled) {
  box-shadow: 0 8px 25px rgba(16, 185, 129, 0.3);
}

.datapingo-button.accent:hover:not(:disabled) {
  box-shadow: 0 8px 25px rgba(139, 92, 246, 0.3);
}

.datapingo-button.warning:hover:not(:disabled) {
  box-shadow: 0 8px 25px rgba(245, 158, 11, 0.3);
}

.datapingo-button:hover:not(:disabled)::before {
  left: 100%;
}

.datapingo-button:active:not(:disabled) {
  transform: translateY(0);
}

.datapingo-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

/* DataPingo Branding Styles */
.brand-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  margin-bottom: 1rem;
}

.company-brand {
  display: flex;
  justify-content: center;
}

.company-logo {
  height: 60px;
  width: auto;
  max-width: 200px;
  object-fit: contain;
}

.product-brand {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.product-logo {
  height: 80px;
  width: auto;
  max-width: 300px;
  object-fit: contain;
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
}

.brand-title {
  font-size: 2rem;
  margin: 0;
  color: var(--text-brand);
  font-weight: 700;
  background: linear-gradient(135deg, var(--datapingo-primary) 0%, var(--datapingo-accent) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Unified Logo Styles */
.unified-brand {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.unified-logo {
  height: 120px;
  width: auto;
  max-width: 400px;
  object-fit: contain;
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
}

.login-title {
  font-size: 2.2rem;
  margin: 0;
  color: var(--text-brand);
  font-weight: 700;
  background: linear-gradient(135deg, var(--datapingo-primary) 0%, var(--datapingo-accent) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-align: center;
  line-height: 1.2;
}

/* Header Branding */
.header-brand {
  display: flex;
  align-items: center;
  gap: 1.25rem; /* Increased gap slightly */
  white-space: nowrap; /* Prevent wrapping */
}

.header-unified-logo {
  height: 45px; /* Slightly smaller */
  width: auto;
  max-width: 180px; /* Slightly smaller max width */
  object-fit: contain;
  border-radius: var(--radius);
}

.header-company-logo {
  height: 40px;
  width: auto;
  max-width: 120px;
  object-fit: contain;
}

.header-product {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.header-icon {
  height: 32px;
  width: 32px;
  object-fit: cover;
  border-radius: var(--radius);
  box-shadow: var(--shadow-sm);
}

.header-title {
  margin: 0;
  font-size: 1.4rem; /* Slightly smaller */
  font-weight: 600;
  background: linear-gradient(135deg, var(--datapingo-primary) 0%, var(--datapingo-secondary) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  line-height: 1.2; /* Tighter line height */
}

/* Feature Icons */
.section-icon {
  height: 20px;
  width: 20px;
  object-fit: cover;
  border-radius: var(--radius-sm);
  margin-right: 0.5rem;
  vertical-align: middle;
}

.feature-icon {
  height: 18px;
  width: 18px;
  object-fit: cover;
  border-radius: var(--radius-sm);
  margin-right: 0.75rem;
  vertical-align: middle;
}

/* Status Icons */
.pending-icon {
  display: flex;
  justify-content: center;
  margin-bottom: 1.5rem;
}

.status-icon {
  height: 64px;
  width: 64px;
  object-fit: cover;
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
  border: 3px solid var(--datapingo-primary);
}

/* Compact Dashboard Layout */
.dashboard-container {
  min-height: 100vh;
  background: var(--background-color);
  line-height: 1.4; /* Reduced from default 1.6 */
}

.dashboard-content {
  padding: 1rem 2rem; /* Reduced from larger padding */
  max-width: 1000px; /* Reduced max width */
  margin: 0 auto;
}

.header {
  background: var(--card-background);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid var(--border-color);
  padding: 1rem 0; /* Remove horizontal padding from header */
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: var(--shadow-sm);
}

/* Header content container to match dashboard layout */
.header-content {
  width: 100%;
  max-width: 1000px; /* Match dashboard-content max-width */
  padding: 0 2rem; /* Match dashboard-content padding */
  display: flex;
  justify-content: space-between; /* Spread elements across the width */
  align-items: center;
}

.step-card {
  background: var(--card-background);
  border-radius: var(--radius-lg);
  padding: 1.5rem; /* Reduced from 2rem+ */
  margin-bottom: 1.5rem; /* Reduced spacing */
  box-shadow: var(--shadow-md);
  border: 1px solid var(--border-color);
}

.step-card h3 {
  margin-bottom: 1rem; /* Reduced margin */
  font-size: 1.3rem; /* Slightly smaller */
  line-height: 1.3;
}

/* Compact Form Elements */
.step-card label {
  margin-bottom: 0.5rem; /* Reduced margin */
  font-size: 0.95rem; /* Slightly smaller */
  line-height: 1.3;
}

.step-card input,
.step-card select,
.step-card textarea {
  margin-bottom: 1rem; /* Reduced margin */
  padding: 0.75rem; /* Reduced padding */
  line-height: 1.4;
}

.step-card > div {
  margin-bottom: 1rem; /* Reduced from 1.5rem */
}

/* Compact Grid Layouts */
.step-card .grid-layout {
  gap: 1rem; /* Reduced gap */
}

/* Compact Monitoring Rules */
.step-card .monitoring-rules .rule-item {
  margin-bottom: 0.75rem; /* Reduced spacing between rules */
  padding: 0.75rem; /* Reduced padding */
}

/* Compact Status Messages */
.step-card .status-message,
.step-card .info-message,
.step-card .warning-message {
  margin-top: 0.5rem; /* Reduced margin */
  margin-bottom: 0.5rem;
  padding: 0.5rem 0.75rem; /* Reduced padding */
  font-size: 0.85rem; /* Smaller font */
  line-height: 1.3;
}

/* Compact Buttons */
.step-card button {
  padding: 0.75rem 1.25rem; /* Reduced padding */
  margin: 0.5rem 0; /* Reduced margin */
  line-height: 1.3;
}

/* Compact User Info */
.user-info {
  display: flex;
  align-items: center;
  gap: 1.5rem; /* Increased gap slightly for better spacing */
  font-size: 0.95rem; /* Slightly larger font */
  white-space: nowrap; /* Prevent wrapping */
}

.user-info span {
  line-height: 1.3;
}

/* Compact Job Cards */
.job-card {
  margin-bottom: 1rem; /* Reduced spacing */
  padding: 1rem; /* Reduced padding */
}

.job-header {
  margin-bottom: 0.75rem; /* Reduced margin */
}

.job-details {
  padding: 1rem; /* Reduced padding */
  gap: 0.75rem; /* Reduced gap */
}

/* More compact typography */
p {
  line-height: 1.4; /* Reduced line height */
  margin-bottom: 0.75rem; /* Reduced margin */
}

small {
  line-height: 1.3;
}

/* Compact feature lists */
ul li {
  padding: 0.25rem 0; /* Reduced padding */
  line-height: 1.4;
}

/* Help Section Styles */
.help-text {
  margin-top: 2rem;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  overflow: hidden;
  background: rgba(248, 250, 252, 0.8);
}

.help-text details {
  border: none;
}

.help-text summary {
  padding: 1rem 1.5rem;
  cursor: pointer;
  background: linear-gradient(135deg, var(--primary-light) 0%, rgba(59, 130, 246, 0.1) 100%);
  font-weight: 600;
  color: var(--text-primary);
  border: none;
  transition: all 0.3s ease;
  list-style: none;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.help-text summary::-webkit-details-marker {
  display: none;
}

.help-text summary:hover {
  background: linear-gradient(135deg, var(--primary-color) 0%, rgba(59, 130, 246, 0.2) 100%);
  color: var(--text-on-primary);
}

.help-text summary::before {
  content: '▶';
  transition: transform 0.3s ease;
  font-size: 0.8rem;
  opacity: 0.7;
}

.help-text details[open] summary::before {
  transform: rotate(90deg);
}

.help-content {
  padding: 2rem 1.5rem;
  background: white;
  border-top: 1px solid var(--border-light);
}

.help-content h4 {
  color: var(--text-primary);
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.help-content ol {
  list-style: none;
  counter-reset: step-counter;
  margin: 0;
  padding: 0;
}

.help-content ol li {
  counter-increment: step-counter;
  margin-bottom: 1.5rem;
  padding-left: 2.5rem;
  position: relative;
  line-height: 1.6;
}

.help-content ol li::before {
  content: counter(step-counter);
  position: absolute;
  left: 0;
  top: 0;
  background: var(--primary-color);
  color: white;
  width: 1.8rem;
  height: 1.8rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8rem;
  font-weight: 600;
}

.help-content ol li strong {
  color: var(--text-primary);
  font-weight: 600;
  display: block;
  margin-bottom: 0.3rem;
}

.help-content code {
  background: rgba(59, 130, 246, 0.1);
  padding: 0.2rem 0.4rem;
  border-radius: 4px;
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
  font-size: 0.85rem;
  color: var(--primary-color);
  border: 1px solid rgba(59, 130, 246, 0.2);
}

.help-note {
  background: linear-gradient(135deg, var(--datapingo-orange-light) 0%, rgba(251, 191, 36, 0.1) 100%);
  border: 1px solid var(--datapingo-orange);
  border-radius: 8px;
  padding: 1rem;
  margin: 1.5rem 0;
  position: relative;
}

.help-note::before {
  content: '💡';
  position: absolute;
  top: -0.5rem;
  left: 1rem;
  background: white;
  padding: 0 0.5rem;
  font-size: 1rem;
}

.help-note strong {
  color: var(--datapingo-orange-dark);
  font-weight: 600;
}

.help-links {
  display: flex;
  gap: 1rem;
  margin-top: 1.5rem;
  flex-wrap: wrap;
}

.help-link {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.6rem 1rem;
  background: white;
  border: 1px solid var(--border-color);
  border-radius: 6px;
  text-decoration: none;
  color: var(--text-secondary);
  font-size: 0.9rem;
  font-weight: 500;
  transition: all 0.3s ease;
}

.help-link:hover {
  background: var(--primary-light);
  color: var(--primary-color);
  border-color: var(--primary-color);
  transform: translateY(-1px);
  box-shadow: 0 2px 8px rgba(37, 99, 235, 0.2);
}

/* Responsive adjustments for help section */
@media (max-width: 768px) {
  .help-text summary {
    padding: 0.8rem 1rem;
    font-size: 0.9rem;
  }
  
  .help-content {
    padding: 1.5rem 1rem;
  }
  
  .help-content ol li {
    padding-left: 2rem;
    margin-bottom: 1.2rem;
  }
  
  .help-content ol li::before {
    width: 1.5rem;
    height: 1.5rem;
    font-size: 0.7rem;
  }
  
  .help-links {
    flex-direction: column;
    gap: 0.8rem;
  }
  
  .help-link {
    justify-content: center;
    padding: 0.8rem;
  }
